import React, { useEffect, useRef, useState } from "react";
  import { Card, CardBody, Dropdown } from "react-bootstrap";
  import { SplideSlide } from "@splidejs/react-splide";
  import SlideSection from "./common/slideSection";
  import NetworkIcon from "../../analytics-new/layout/common/networkIcon";
  import { Link, useNavigate } from "react-router-dom";
  import { getWidgetWallData } from "../../../../actions/wallDataAction";
  import { shallowEqual, useDispatch } from "react-redux";
  import { getWallFeeds } from "../../../../actions/display/analytics";
  import { toggleAddFeedsSection_D } from "../../../../actions/display/showDisplayModalAction";
  import { openCreateNewWallPopup } from "../../../../actions/display/wallsAction";
  import DropdownComponet from "../../walls/component/modal/Dropdown";
  import WallsSkeleton from "./skeleton/wallsSkeleton";
  import HoverVideoPlayer from "react-hover-video-player";
  import { baseUrl, kFormatter } from "../../../../utils";
  import CreateWall from "./common/createWall";
  import { useSelector } from "react-redux";
  import PercentDifference from "../../analytics-new/layout/common/percentDifference";

  const CreatedWalls = (props) => {
    const { allWalllist, loader } = props;
    const { userData } = useSelector(
      (state) => ({
        wallReducer: state.wallReducer,
        userData: state.authenticated.userData,
      }),
      shallowEqual
    );

    const splideRef = useRef(null);
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [upgrade, setUpgrade] = React.useState(false);
    const [updateSlider, setupdateSlider] = React.useState(0);
    const [modalData, setModalData] = React.useState({
      profanity: 0,
      modalShow: false,
      name: "",
      url: "",
      wall_id: null,
      autoUpdate: 1,
      rename_status: 0,
      callbackWall: 0,
    });

    const [activeWall, setactiveWall] = useState(0);
    const { user, UserPlan } = props.userData;

    const callbackWall = (status) => {};

    const handleCategoryClick = (startIndex) => {
      if (splideRef.current) splideRef.current.splide.go(startIndex); // Move to the specific slide
    };

    const displayWindowOpen = (wallId) => {
      dispatch(getWidgetWallData(user.ownerId, wallId));
      dispatch(getWallFeeds(user.ownerId));
      navigate(`/wall/preview/${wallId}`);
    };

    
    const nevigationRedirect = (activeIndex, wallId = 0, nevigation = 0, feedId = 0) => {

      if (allWalllist.length != 1 && !activeIndex) return;
     
      switch (nevigation) {
        case 1:
          navigate(`/moderation/index/${wallId}`);
        localStorage.setItem("wallId", wallId);
          break;
          
        case 2:
          const analyticPath = ![58].includes(UserPlan?.planId) ? "/analytic-new/" : "analytic/";
          navigate(`${analyticPath}${wallId}`);
          dispatch(getWidgetWallData(user?.ownerId, wallId));
          break;
          
        case 3:
          displayWindowOpen(wallId);
          break;
          
        case 4:
          if (!feedId?.length) dispatch(toggleAddFeedsSection_D(true));
          dispatch(getWidgetWallData(user?.ownerId, wallId));
          navigate(`/wall/index/${wallId}`);
          break;

        default:
          break;
      }
    };

    const clickToUpdateState = (data) => (event) => {
      dispatch(openCreateNewWallPopup(false));
      const widgetCount =
        allWalllist && allWalllist.length ? allWalllist.length : 0;
      if (widgetCount < userData.UserRule.walls || data.rename_status == 1)
        setModalData(data);
      else setUpgrade(true);
    };


    
    const handleResize = () => setupdateSlider(Math.random())

    useEffect(() => {
      window.addEventListener("resize", handleResize);

      return () => {
          window.removeEventListener("resize", handleResize);
      };
    }, []);


     return (
      <div className="created-walls-section mb-5">
        <Card className="p-0 card-custom card-stretch">
          {(allWalllist && Object.keys(allWalllist).length) || loader ? (
            <>
              <Card.Header className="p-5">
                <div className="m-0 d-flex align-items-center justify-content-between card-title h5 w-100">
                  <h6 class="card-label font-weight-bolder m-0 ">
                    My Walls
                  </h6>
                  {!loader ? (
                    <div className="">
                      <Link
                        to={"/walls"}
                        className="text-secondary font-size-base"
                      >
                        View All
                      </Link>
                    </div>
                  ) : null}
                </div>
              </Card.Header>

              <CardBody className="p-5">
                {loader ? (
                  <WallsSkeleton />
                ) : (
                  <>
                    <div className="row mb-5 ">
                      <div className="col-sm-8">
                        <div className="custom-pagination">
                          {Object.keys(allWalllist).length &&
                            allWalllist.map((e, index) => (
                              <button
                                className={`btn ${
                                  activeWall == index ||
                                  Object.keys(allWalllist).length == 1
                                    ? "btn-primary"
                                    : "btn-outline-dark"
                                } me-2 mb-1 text-capitalize overflow-hidden text-nowrap text-ellipsis text-capitalize mx-w-200`}
                                key={index}
                                onClick={() => {
                                  handleCategoryClick(index);
                                  setactiveWall(index);
                                }}
                              >
                                {e.name}
                              </button>
                            ))}
                        </div>
                      </div>
                      <div className="col-sm-4">
                        <div className="custom_primary_secondary d-flex justify-content-end mt-1">
                          <CreateWall
                            modalData={modalData}
                            widgetWallsData={allWalllist}
                            upgrade={upgrade}
                            clickToUpdateState={clickToUpdateState}
                            setUpgrade={setUpgrade}
                            setupdateWall={""}
                            setModalData={setModalData}
                            isCollaboratorRole={JSON.parse(
                              localStorage.getItem("isCollaborator")
                            )}
                          />
                        </div>
                      </div>
                    </div>

                    {/* Splide Slider */}

                    <SlideSection
                      section={1}
                      key={updateSlider}
                      totalRecord={allWalllist.length}
                      splideRef={splideRef}
                      setactiveWall={setactiveWall}
                    >
                      {allWalllist.map((e, index) => {

                        const activewallIndex = activeWall===index
                        return (
                          <SplideSlide
                            data-feedList={e.feedList}
                            data-wallId={e.id}
                            className={`splide__slide_walls`}
                            key={index}
                          >
                            <div
                              key={activeWall + index}
                              className={`created-walls-card h-100 splide__slide-${index}`}
                            >
                              <div className={`created-walls-card-inner h-100 p-5 ${(activeWall === index || allWalllist.length==1) ? 'overlay-hide' : ''}`}>
                                <div className="row">
                                  <div className="col-md-6 nevigation-wall">
                                    <div className="wall_cont_wrap innter_wall_cont_wrap_ cursor-pointer created-walls-card-img p-0">
                                      <a
                                        to={`#`}
                                        onClick={() =>  nevigationRedirect(activewallIndex,
                                                e.id,
                                                4,
                                                e.feedList
                                              )
                                          }
                                        className="card card-custom overlay shadow-none overflow-hidden rounded-0"
                                      >
                                        {(activeWall === index || allWalllist.length==1) ? (
                                          <>
                                            <video
                                            className="tb_post_modal_video w-100 h-100 mx-h-470"
                                            muted
                                            autoPlay={true}
                                            data-type="video"
                                            data-load={0}
                                            loop
                                            src={`${baseUrl(
                                              `theme-media/theme-video/${e.signageTheme}.mp4?v=45445`
                                            )}`}
                                            style={{
                                              width: "100%",
                                              paddingTop: "-10px",
                                            }}
                                          ></video>  <img
                                          role="img"
                                          src={`${baseUrl(
                                            `theme-media/theme-thumb/${e.signageTheme}.png`
                                          )}`}
                                          height={260}
                                          width={"100%"}
                                        />
                                          </>
                                        ) : (
                                          <>
                                           <HoverVideoPlayer className="position-absolute h-100"
                                            videoSrc={`${baseUrl(
                                              `theme-media/theme-video/${e.signageTheme}.mp4?v=454`
                                            )}`}
                                            width={391}
                                            height={300}
                                            style={{
                                              width: "100%",
                                              paddingTop: "-10px",
                                            }}
                                          /> <img
                                          role="img"
                                          src={`${baseUrl(
                                            `theme-media/theme-thumb/${e.signageTheme}.png`
                                          )}`}
                                          height={260}
                                          width={"100%"}
                                        />
                                          </>
                                        )}

                                        <div className="overlay-layer">
                                          <img
                                            src={baseUrl(
                                              `dashboard/icons/play-icon.svg`
                                            )}
                                            height={46}
                                            width={46}
                                            className="img-fluid"
                                            alt="play"
                                          />
                                        </div>
                                      </a>
                                    </div>
                                  </div>
                                  <div className="col-md-6">
                                    <div className="mt-3 d-flex flex-column justify-content-between h-100">
                                      <div className="wall_section-2">
                                        <div className="d-flex justify-content-between mb-8">
                                          <a
                                            to={`#`}
                                            onClick={() =>
                                            (activeWall === index || allWalllist.length==1)
                                                ? nevigationRedirect(activewallIndex,
                                                    e.id,
                                                    4,
                                                    e.feedList
                                                  )
                                                : {}
                                            }
                                          >
                                            <div className="">
                                              <h5 className="mb-2 text-capitalize d-flex align-items-center nevigation-wall cursor-pointer">
                                                {e.name}
                                                {e.activeScreen ? (
                                                  <span className="status_active"></span>
                                                ) : null}
                                              </h5>
                                              <p className="text-secondary text-capitalize m-0 cursor-pointer">
                                                {`${e.themeName} : ${e.themeLayout}`}
                                              </p>
                                            </div>
                                          </a>
                                          <div className="">
                                            <p className="text-secondary font-weight-bold font-size-h6 m-0">
                                              Posts: {kFormatter(Math.round(e.totalPostCount))}

                                              { e.difference ?  
                                                <PercentDifference difference={e.difference} flag={e.flagColor} /> : null }  
                                            </p>
                                          </div>
                                        </div>
                                        {e.feedList &&
                                        Object.keys(e.feedList).length ? (
                                          <div className="created-walls-network mb-8">
                                            <ul className="m-0">
                                              {e.feedList.map((fdata, key) => {
                                                return (
                                                  <li className="d-flex align-items-center mb-4 ">
                                                    <NetworkIcon
                                                      isDashboard={
                                                        "me-2 text-center text-white"
                                                      }
                                                      icon={fdata.socialIcon}
                                                    />
                                                    { fdata.name.replace(/<i[^>]*>(.*?)<\/i>/g, '') }
                                                  </li>
                                                );
                                              })}
                                            </ul>
                                          </div>
                                        ) : null}
                                      </div>

                                      <div className="d-flex align-items-center justify-content-end mb-3">
                                        <div className="created-walls-report d-flex align-items-center flex-wrap">
                                          <a
                                            onClick={() =>
                                              nevigationRedirect(activewallIndex,e.id, 1)
                                            }
                                            href="#"
                                            className="btn btn-secondary d-flex align-items-center text-secondary me-2 border-0"
                                          >
                                            <svg
                                              xmlns="http://www.w3.org/2000/svg"
                                              className="me-2"
                                              width={"20px"}
                                              height={"20px"}
                                              viewBox="0 0 24 24"
                                              fill="currentColor"
                                            >
                                              <path d="M6.99979 7V3C6.99979 2.44772 7.4475 2 7.99979 2H20.9998C21.5521 2 21.9998 2.44772 21.9998 3V16C21.9998 16.5523 21.5521 17 20.9998 17H17V20.9925C17 21.5489 16.551 22 15.9925 22H3.00728C2.45086 22 2 21.5511 2 20.9925L2.00276 8.00748C2.00288 7.45107 2.4518 7 3.01025 7H6.99979ZM8.99979 7H15.9927C16.549 7 17 7.44892 17 8.00748V15H19.9998V4H8.99979V7ZM15 9H4.00255L4.00021 20H15V9ZM8.50242 18L4.96689 14.4645L6.3811 13.0503L8.50242 15.1716L12.7451 10.9289L14.1593 12.3431L8.50242 18Z"></path>
                                            </svg>{" "}
                                            Moderate
                                          </a>
                                          <a
                                            href="#"
                                            onClick={() =>
                                              nevigationRedirect(activewallIndex,e.id, 2)
                                            }
                                            className="btn btn-secondary d-flex align-items-center text-secondary me-2 border-0"
                                          >
                                            <svg
                                              xmlns="http://www.w3.org/2000/svg"
                                              className="me-2"
                                              viewBox="0 0 24 24"
                                              width={"20px"}
                                              height={"20px"}
                                              fill="currentColor"
                                            >
                                              <path d="M2 13H8V21H2V13ZM16 8H22V21H16V8ZM9 3H15V21H9V3ZM4 15V19H6V15H4ZM11 5V19H13V5H11ZM18 10V19H20V10H18Z"></path>
                                            </svg>{" "}
                                            Analytics
                                          </a>
                                          <a
                                            href="#"
                                            onClick={() =>
                                              nevigationRedirect(activewallIndex,e.id, 3)
                                            }
                                            className="btn btn-secondary d-flex align-items-center text-secondary me-2 border-0"
                                          >
                                            <svg
                                              xmlns="http://www.w3.org/2000/svg"
                                              className="me-2"
                                              viewBox="0 0 24 24"
                                              width={"20px"}
                                              height={"20px"}
                                              fill="currentColor"
                                            >
                                              <path d="M4 16H20V5H4V16ZM13 18V20H17V22H7V20H11V18H2.9918C2.44405 18 2 17.5511 2 16.9925V4.00748C2 3.45107 2.45531 3 2.9918 3H21.0082C21.556 3 22 3.44892 22 4.00748V16.9925C22 17.5489 21.5447 18 21.0082 18H13Z"></path>
                                            </svg>{" "}
                                            Display
                                          </a>
                                          <a
                                            href="#"
                                            onClick={() =>
                                              (activeWall === index || allWalllist.length==1)
                                                  ? nevigationRedirect(activewallIndex,
                                                      e.id,
                                                      4,
                                                      e.feedList
                                                    )
                                                  : {}
                                              }
                                            className="btn btn-secondary d-flex align-items-center text-secondary me-2 border-0"
                                          >
                                            <svg xmlns="http://www.w3.org/2000/svg" className="me-2" width={"20px"} height={"20px"} viewBox="0 0 24 24" fill="currentColor"><path d="M16.7574 2.99678L14.7574 4.99678H5V18.9968H19V9.23943L21 7.23943V19.9968C21 20.5491 20.5523 20.9968 20 20.9968H4C3.44772 20.9968 3 20.5491 3 19.9968V3.99678C3 3.4445 3.44772 2.99678 4 2.99678H16.7574ZM20.4853 2.09729L21.8995 3.5115L12.7071 12.7039L11.2954 12.7064L11.2929 11.2897L20.4853 2.09729Z"></path></svg>{" "}
                                            Edit
                                          </a>
                                        </div>


                                        <div className="">
                                          <button
                                            type="button"
                                            className="p-0 border-0 bg-transparent mb-2"
                                          >
                                            <div className="card-toolbar">
                                              <Dropdown
                                                onClick={() =>
                                                  nevigationRedirect(activewallIndex)
                                                }
                                                align={"end"}
                                              >
                                                <Dropdown.Toggle className="btn btn-link btn-icon btn-sm btn-clean btn-after-none w-auto">
                                                  <svg
                                                    xmlns="http://www.w3.org/2000/svg"
                                                    height={"20px"}
                                                    width={"20px"}
                                                    viewBox="0 0 24 24"
                                                    fill="currentColor"
                                                  >
                                                    <path d="M12 3C10.9 3 10 3.9 10 5C10 6.1 10.9 7 12 7C13.1 7 14 6.1 14 5C14 3.9 13.1 3 12 3ZM12 17C10.9 17 10 17.9 10 19C10 20.1 10.9 21 12 21C13.1 21 14 20.1 14 19C14 17.9 13.1 17 12 17ZM12 10C10.9 10 10 10.9 10 12C10 13.1 10.9 14 12 14C13.1 14 14 13.1 14 12C14 10.9 13.1 10 12 10Z"></path>
                                                  </svg>
                                                </Dropdown.Toggle>
                                                <Dropdown.Menu
                                                  style={{ width: 175 }}
                                                >
                                                  <DropdownComponet
                                                    callbackWall={callbackWall}
                                                    props={{
                                                      wallsData: e,
                                                      clickToUpdateState:
                                                        clickToUpdateState,
                                                    }}
                                                    clickToUpdateState={
                                                      clickToUpdateState
                                                    }
                                                    ownerId={user.ownerId}
                                                    userData={props.userData}
                                                  />
                                                </Dropdown.Menu>
                                              </Dropdown>
                                            </div>
                                          </button>
                                        </div>
                                      </div>
                                    </div>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </SplideSlide>
                        );
                      })}
                    </SlideSection>
                  </>
                )}
              </CardBody>
            </>
          ) : (
            <div className="col-12">
              <div className="d-flex justify-content-center w-100">
                <button
                  type="button"
                  onClick={() =>
                    clickToUpdateState({
                      modalShow: true,
                      wall_id: null,
                      autoUpdate: 1,
                      callbackWall: 1,
                    })
                  }
                  className="btn btn-link d-flex flex-wrap flex-column align-content-center add_card_button create-new-wall cursor-pointer px-5 w-100">
                  <CreateWall
                    firstWall={1}
                    widgetWallsData={allWalllist}
                    modalData={modalData}
                    upgrade={upgrade}
                    clickToUpdateState={clickToUpdateState}
                    setUpgrade={setUpgrade}
                    setupdateWall={""}
                    setModalData={setModalData}
                    isCollaboratorRole={JSON.parse(
                      localStorage.getItem("isCollaborator")
                    )}
                  />
                </button>
              </div>
            </div>
          )}
        </Card>
      </div>
    );
  };

  export default React.memo(CreatedWalls);
