import React from "react";

const Network = () => {
  return (
    <div class="d-flex align-items-center flex-grow-1 mb-3">
      <div class="symbol symbol-45 symbol-light me-2 social_user_img">
        <span class="sk_img"></span>
      </div>
      <div class="d-flex text-left flex-column conn_user_info w-25">
        <div class="sk_line sk_line_height_12"></div>
      </div>
    </div>
  );
};

const Action = () => {
  return <div class="sk_line sk_line_height_28 me-2"></div>;
};

const WallsSkeleton = () => {
  return (
    <div class="d-flex align-items-center flex-wrap justify-content-between">
      {[1, 2].map(() => {
        return (
          <div className="col-6 px-2">
            <div class="ex-graphmin-height w-100">
              <div class="ex-recentwbox d-block p-4">
                <div className="row">
                  <div className="col-6">
                    <div class="ex-recentwright w-100 mb-3">
                      <div
                        class="sk_rect btn-min-w sk_img_thumb"
                        style={{ paddingBottom: "55%" }}
                      ></div>
                    </div>
                  </div>
                  <div className="col-6">
                    <div class="w-100">
                      <div class="ex-recentwleft w-50">
                        <div class="sk_line sk_line_height_12"></div>
                        <div class="sk_line sk_line_80 sk_line_height_8"></div>
                      </div>
                      <div className="mt-5">
                        <Network />
                        <Network />
                        <Network />
                      </div>
                      <div class="ex-recentwright d-flex w-100">
                        <Action />
                        <Action />
                        <Action />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default React.memo(WallsSkeleton);
